.SearchBar {
    display: flex;
    margin-bottom: 15px;
}

#searchTextArea {
    justify-content: center;
    align-content: center;
    text-align: center;
}

.find-game-review-search-bar {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px;
    margin-top: 5px;
}

.find-game-review-container {
    margin-top: 130px;
    padding: 10px;
}

.find-game-review-card-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    border-radius: 15px;
    background-color: #45484d;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#45484d), to(#121111));
    background-image: -webkit-linear-gradient(bottom, #45484d, #121111);
    background-image: -moz-linear-gradient(bottom, #45484d, #121111);
    background-image: -ms-linear-gradient(bottom, #45484d, #121111);
    background-image: -o-linear-gradient(bottom, #45484d, #121111);
    background-image: linear-gradient(to bottom, #45484d, #121111);
    filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, startColorstr=#45484d, endColorstr=#121111);
}

.find-game-review-image {
    width: 100%;
    height: 200px;
    border-radius: 15px 15px 0px 0px;
}

.find-game-review-title {
    padding-left: 10px;
    color: white;
}

.find-game-review-text {
    text-align: left;
    word-break: break-all;
    width: 93%;
    padding-left: 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: white;
}

.find-game-review-author {
    padding-right: 15px;
    text-align: right;
    color: white;
}


@media only screen and (min-width: 720px) {
    .find-game-review-card-container {
        padding: 10px;
        width: 1000px;
        margin: auto;
        margin-top: 130px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        position: relative;
        right: 20px;

    }

    .find-game-review-card-container {
        width: 450px;
        display: flex;
        flex-direction: column;
        margin-bottom: 10px;
        border-radius: 15px;
        background-color: #45484d;
        background-image: -webkit-gradient(linear, left top, left bottom, from(#45484d), to(#121111));
        background-image: -webkit-linear-gradient(bottom, #45484d, #121111);
        background-image: -moz-linear-gradient(bottom, #45484d, #121111);
        background-image: -ms-linear-gradient(bottom, #45484d, #121111);
        background-image: -o-linear-gradient(bottom, #45484d, #121111);
        background-image: linear-gradient(to bottom, #45484d, #121111);
        filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, startColorstr=#45484d, endColorstr=#121111);
    }
}